.nav {
  align-items: center;
  background-color: transparent;
  display: flex;
  height: 3.2em;
  justify-content: space-between;
  left: 0;
  position: fixed;
  top: 0;
  transition: background-color 1s;
  width: 100vw;
  z-index: 5; }
  .nav--tinted {
    background-color: rgba(0, 0, 0, 0.2); }
  .nav a {
    color: inherit; }
    .nav a:hover, .nav a:active, .nav a:focus {
      color: inherit;
      text-decoration: inherit; }

.nav-links {
  align-items: center;
  display: flex;
  font-size: 0.8em;
  font-weight: 400;
  height: 100%;
  letter-spacing: 0.225em;
  list-style: none;
  margin: 0;
  padding: 0; }
  .nav-links__link {
    border-bottom: solid 3px transparent;
    align-items: center;
    display: flex;
    height: calc(100% - 3px);
    margin: 0 1.5em;
    transition: border-bottom-color 250ms; }
    .nav-links__link--hoverable:hover {
      border-bottom: solid 3px #ef5e4a; }

.nav-button {
  background-color: #1fc1ad;
  border-radius: 100px;
  font-weight: 600;
  height: 3.125em;
  padding: 0 2.75em;
  line-height: 3.125em;
  margin: 0 1.5em;
  transition: background-color 250ms; }
  .nav-button:hover, .nav-button:focus, .nav-button:active {
    background-color: #ef5e4a; }



